import type { AxiosResponse } from 'axios'
import { request } from '@/utils/request'
import type { IResponse, IListResp } from '@/types/response'

const api = {
  list: '/labels/list',
  create: '/labels/create',
  update: '/labels/update',
  delete: '/labels/delete',
  batchCreate: '/labels/batch',
  getLabels: '/labels/list',
  merge:'/labels/merge'
}

export function mergeLabelApi(data: CreateLabelParams) {
  return request.post<IResponse<Label>>(api.merge, data)
}
// 标签接口参数类型定义
export interface Label {
  id: number
  name: string
  color: number[]  // RGBA颜色值，如 [255, 0, 0, 255]
  project_id: number
  created_at: string
  updated_at: string
}

export interface CreateLabelParams {
  name: string
  color: number[]  // RGBA颜色值，如 [255, 0, 0, 255]
  project_id: number
}

export interface UpdateLabelParams {
  label_id: number
  name: string
  color?: number[]  // RGBA颜色值，如 [255, 0, 0, 255]
  project_id: number
}

export interface DeleteLabelParams {
  id: number
}

export interface LabelItem {
  name: string  // 标签名称
  color: number[]  // RGBA颜色值，如 [255, 0, 0, 255]
}

export interface BatchCreateLabelsParams {
  project_id: number
  labels: LabelItem[]
}

export interface ProjectLabelsParams {
  project_id: number
  page?: number
  page_size?: number
}

/**
 * 获取标签列表
 * @param params 查询参数
 * @returns 标签列表
 */
export function getLabels(params: ProjectLabelsParams) {
  return request.get<IResponse<IListResp<Label>>>(api.getLabels, { params })
}

/**
 * 创建标签
 * @param data 创建标签参数
 * @returns 创建的标签
 */
export function createLabel(data: CreateLabelParams) {
  return request.post<IResponse<Label>>(api.create, data)
}

/**
 * 批量创建标签
 * @param data 批量创建标签参数
 * @returns 创建结果
 */
export function batchCreateLabels(data: BatchCreateLabelsParams) {
  return request.post<IResponse<{ success: boolean; created_count: number }>>(api.batchCreate, data)
}

/**
 * 更新标签
 * @param data 更新标签参数
 * @returns 更新的标签
 */
export function updateLabel(data: UpdateLabelParams) {
  return request.post<IResponse<Label>>(api.update, data)
}

/**
 * 删除标签
 * @param data 删除标签参数
 * @returns 删除结果
 */
export interface DeleteLabelParams {
  project_id: number
  label_id: number
}

export function deleteLabel(data: DeleteLabelParams) {
  return request.post<IResponse<{ success: boolean }>>(api.delete, data)
}
